@use '~ag-grid-community/styles/ag-grid.min.css';
@use '~ag-grid-community/styles/ag-theme-material.min.css';

:root {
    --colorStatusColWidth: 12px;
}

.ag-header-cell {
    height: 48px;
}
.ag-cell,
.ag-header-cell {
    padding-left: 13px !important;
    padding-right: 13px !important;
}

.ag-header-cell,
.ag-cell,
atui-menu {
    atui-checkbox[aria-checked] {
        background-color: transparent;
    }
}

.ag-header-cell[col-id='colorStatusCell'],
.ag-cell[col-id='colorStatusCell'] {
    padding-left: 0 !important;
    padding-right: 0 !important;
    width: var(--colorStatusColWidth) !important;
    border: none !important;
}
